'use client';

import React, { useEffect, useMemo, useState } from 'react';
import { useParams, useRouter } from 'next/navigation';
import { Topbar } from '../../../../components/Topbar';
import { createClient } from '../../../../lib/supabaseBrowser';

type WorkoutTypeUI = 'rodagem' | 'progressivo' | 'alternado';
type IntensityUI = 'leve' | 'moderado' | 'forte';

type Block = { distance_km: number; intensity: IntensityUI | string };

type WorkoutRow = {
  id: string;
  student_id: string;
  trainer_id: string;
  status: string | null;
  title: string | null;
  template_type: string | null;
  include_warmup: boolean | null;
  warmup_km: number | null;
  include_cooldown: boolean | null;
  cooldown_km: number | null;
  blocks: any;
  total_km: number | null;
  share_slug: string | null;
};

const WORKOUT_TYPE_LABEL: Record<WorkoutTypeUI, string> = {
  rodagem: 'Rodagem',
  progressivo: 'Progressivo',
  alternado: 'Alternado',
};

const INTENSITY_LABEL: Record<IntensityUI, string> = {
  leve: 'Leve',
  moderado: 'Moderado',
  forte: 'Forte',
};

const TEMPLATE_TYPE_CANDIDATES: Record<WorkoutTypeUI, string[]> = {
  rodagem: ['rodagem', 'easy', 'easy_run', 'run'],
  progressivo: ['progressivo', 'progressive', 'progressive_run'],
  alternado: ['alternado', 'interval', 'intervals', 'alternating'],
};

function kmToNumber(val: string): number {
  const n = Number(val.replace(',', '.'));
  return Number.isFinite(n) ? n : NaN;
}

function clampKm(x: number, min = 0.5, max = 200): number {
  if (!Number.isFinite(x)) return min;
  return Math.min(max, Math.max(min, x));
}

function safeBlocks(val: any): Block[] {
  if (!val) return [];
  if (Array.isArray(val)) return val as Block[];
  return [];
}

function randomSlug(prefix: string): string {
  const rand = Math.random().toString(36).slice(2, 8);
  const ts = Date.now().toString(36).slice(-6);
  return `${prefix}-${ts}-${rand}`;
}

function slugify(text: string): string {
  return text
    .toLowerCase()
    .normalize('NFD')
    .replace(/[\u0300-\u036f]/g, '')
    .replace(/[^a-z0-9]+/g, '-')
    .replace(/^-+|-+$/g, '')
    .slice(0, 60);
}

function normalizeTemplateType(db: string | null | undefined): WorkoutTypeUI {
  const v = String(db ?? '').toLowerCase();
  if (['progressivo', 'progressive'].includes(v)) return 'progressivo';
  if (['alternado', 'interval', 'intervals'].includes(v)) return 'alternado';
  return 'rodagem';
}

function normalizeIntensity(db: string | null | undefined): IntensityUI {
  const v = String(db ?? '').toLowerCase();
  if (['moderado', 'moderate'].includes(v)) return 'moderado';
  if (['forte', 'hard'].includes(v)) return 'forte';
  return 'leve';
}

export default function Page() {
  const supabase = useMemo(() => createClient(), []);
  const router = useRouter();
  const params = useParams();
  const workoutId = String((params as any)?.id ?? '');

  const [loading, setLoading] = useState(true);
  const [errorMsg, setErrorMsg] = useState<string | null>(null);
  const [workout, setWorkout] = useState<WorkoutRow | null>(null);

  const [title, setTitle] = useState('');
  const [workoutType, setWorkoutType] = useState<WorkoutTypeUI>('rodagem');

  const [includeWarmup, setIncludeWarmup] = useState(false);
  const [warmupKmRaw, setWarmupKmRaw] = useState('1');

  const [blocks, setBlocks] = useState<Block[]>([]);
  const [includeCooldown, setIncludeCooldown] = useState(false);
  const [cooldownKmRaw, setCooldownKmRaw] = useState('1');

  const [isSaving, setIsSaving] = useState(false);

  const totalKm = useMemo(() => {
    const w = includeWarmup ? clampKm(kmToNumber(warmupKmRaw)) : 0;
    const c = includeCooldown ? clampKm(kmToNumber(cooldownKmRaw)) : 0;
    const b = blocks.reduce((sum, blk) => sum + clampKm(Number(blk.distance_km), 0, 200), 0);
    return Math.round((w + b + c) * 10) / 10;
  }, [includeWarmup, warmupKmRaw, includeCooldown, cooldownKmRaw, blocks]);

  useEffect(() => {
    let cancelled = false;

    async function load() {
      setLoading(true);
      setErrorMsg(null);

      const { data: auth } = await supabase.auth.getUser();
      const uid = auth?.user?.id ?? null;
      if (!uid) {
        if (!cancelled) {
          setErrorMsg('Voc√™ precisa estar logado para editar o treino.');
          setLoading(false);
        }
        return;
      }

      const { data, error } = await supabase
        .from('workouts')
        .select(
          'id,student_id,trainer_id,status,title,template_type,include_warmup,warmup_km,include_cooldown,cooldown_km,blocks,total_km,share_slug'
        )
        .eq('id', workoutId)
        .maybeSingle();

      if (cancelled) return;

      if (error) {
        setErrorMsg(error.message);
        setLoading(false);
        return;
      }
      if (!data) {
        setErrorMsg('Treino n√£o encontrado.');
        setLoading(false);
        return;
      }

      if ((data as any).trainer_id && (data as any).trainer_id !== uid) {
        setErrorMsg('Voc√™ n√£o tem permiss√£o para editar este treino.');
        setLoading(false);
        return;
      }

      const row = data as WorkoutRow;
      setWorkout(row);

      setTitle(row.title ?? '');
      setWorkoutType(normalizeTemplateType(row.template_type));
      setIncludeWarmup(Boolean(row.include_warmup));
      setWarmupKmRaw(String(row.warmup_km ?? 1));

      const blk = safeBlocks(row.blocks).map((b) => ({
        distance_km: Number(b.distance_km ?? 1),
        intensity: normalizeIntensity(String(b.intensity)),
      }));
      setBlocks(blk.length ? blk : [{ distance_km: 5, intensity: 'leve' }]);

      setIncludeCooldown(Boolean(row.include_cooldown));
      setCooldownKmRaw(String(row.cooldown_km ?? 1));

      setLoading(false);
    }

    void load();

    return () => {
      cancelled = true;
    };
  }, [supabase, workoutId]);

  function addBlock() {
    setBlocks((prev) => [...prev, { distance_km: 1, intensity: 'leve' }]);
  }

  function removeBlock(index: number) {
    setBlocks((prev) => prev.filter((_, i) => i !== index));
  }

  function updateBlock(index: number, patch: Partial<Block>) {
    setBlocks((prev) => prev.map((b, i) => (i === index ? { ...b, ...patch } : b)));
  }

  async function handleSave() {
    if (!workout) return;

    setIsSaving(true);
    setErrorMsg(null);

    try {
      const warmupKm = includeWarmup ? clampKm(kmToNumber(warmupKmRaw)) : null;
      const cooldownKm = includeCooldown ? clampKm(kmToNumber(cooldownKmRaw)) : null;

      const basePayload = {
        title: title.trim() ? title.trim() : null,
        include_warmup: includeWarmup,
        warmup_km: warmupKm,
        include_cooldown: includeCooldown,
        cooldown_km: cooldownKm,
        blocks,
        total_km: totalKm,
      };

      const candidates = TEMPLATE_TYPE_CANDIDATES[workoutType] ?? [workoutType];
      let lastError: any = null;

      for (const template_type of candidates) {
        const { error } = await supabase
          .from('workouts')
          .update({ ...basePayload, template_type })
          .eq('id', workout.id);

        if (!error) {
          router.push(`/students/${workout.student_id}`);
          return;
        }
        lastError = error;
      }

      throw lastError ?? new Error('Falha ao salvar altera√ß√µes.');
    } catch (e: any) {
      setErrorMsg(e?.message ?? 'Falha ao salvar altera√ß√µes.');
    } finally {
      setIsSaving(false);
    }
  }

  async function handleShare() {
    if (!workout) return;

    setIsSaving(true);
    setErrorMsg(null);

    try {
      const slug =
        workout.share_slug ??
        randomSlug(slugify(title || WORKOUT_TYPE_LABEL[workoutType]) || 'treino');

      const { error } = await supabase
        .from('workouts')
        .update({ status: 'shared', share_slug: slug })
        .eq('id', workout.id);

      if (error) throw error;

      const url = `${window.location.origin}/w/${slug}`;
      try {
        await navigator.clipboard.writeText(url);
      } catch {
        // ignore
      }

      router.push(`/students/${workout.student_id}`);
    } catch (e: any) {
      setErrorMsg(e?.message ?? 'Falha ao compartilhar.');
    } finally {
      setIsSaving(false);
    }
  }

  return (
    <div className="min-h-screen bg-[#07150f] text-white">
      <Topbar title="Editar Treino" showBack onBack={() => router.back()} />

      <div className="max-w-2xl mx-auto p-4 pb-24 space-y-4">
        {loading ? (
          <Card>Carregando‚Ä¶</Card>
        ) : errorMsg ? (
          <div className="rounded-xl border border-red-900/40 bg-red-950/30 p-3 text-red-200">
            {errorMsg}
          </div>
        ) : workout ? (
          <>
            <Card>
              <div className="font-semibold mb-2">Informa√ß√µes b√°sicas</div>
              <Input value={title} onChange={setTitle} placeholder="Ex: Rodagem leve" />
              <div className="mt-2 text-sm text-white/70">
                Dist√¢ncia total estimada: {totalKm.toFixed(1)} km
              </div>
            </Card>

            <Card>
              <div className="font-semibold mb-3">Tipo de Treino</div>
              <div className="flex gap-2">
                {(['rodagem', 'progressivo', 'alternado'] as WorkoutTypeUI[]).map((t) => (
                  <Toggle
                    key={t}
                    active={workoutType === t}
                    onClick={() => setWorkoutType(t)}
                    label={WORKOUT_TYPE_LABEL[t]}
                  />
                ))}
              </div>
            </Card>

            <Card>
              <div className="font-semibold mb-3">Estrutura</div>

              <div className="flex items-center justify-between mb-2">
                <div className="flex items-center gap-2">
                  <span className="text-lg">üî•</span>
                  <div className="font-semibold">Aquecimento</div>
                </div>
                <Switch checked={includeWarmup} onChange={setIncludeWarmup} />
              </div>

              {includeWarmup ? (
                <div className="grid grid-cols-[1fr_auto_1fr] gap-3 items-end mb-4">
                  <div>
                    <div className="text-xs tracking-widest text-white/60 mb-1">DIST√ÇNCIA</div>
                    <Input value={warmupKmRaw} onChange={setWarmupKmRaw} inputMode="decimal" />
                  </div>
                  <div className="pb-2 text-white/60">km</div>
                  <div>
                    <div className="text-xs tracking-widest text-white/60 mb-1">RITMO</div>
                    <div className="rounded-xl border border-white/10 bg-white/5 px-3 py-2 text-white/80">
                      Livre
                    </div>
                  </div>
                </div>
              ) : null}

              <div className="flex items-center justify-between">
                <div className="font-semibold">Blocos principais</div>
                <Button onClick={addBlock} variant="ghost">
                  + Adicionar bloco
                </Button>
              </div>

              <div className="mt-3 space-y-3">
                {blocks.map((blk, idx) => (
                  <div key={idx} className="rounded-2xl border border-white/10 bg-white/5 p-3">
                    <div className="flex items-center justify-between mb-2">
                      <div className="text-sm font-semibold">Bloco {idx + 1}</div>
                      <button
                        type="button"
                        onClick={() => removeBlock(idx)}
                        className="text-red-300/90 hover:text-red-200"
                        aria-label="Remover bloco"
                      >
                        üóëÔ∏è
                      </button>
                    </div>

                    <div className="grid grid-cols-[1fr_auto_1fr] gap-3 items-end">
                      <div>
                        <div className="text-xs tracking-widest text-white/60 mb-1">DIST√ÇNCIA DO TRECHO</div>
                        <Input
                          value={String(blk.distance_km)}
                          onChange={(v) => {
                            const n = kmToNumber(v);
                            updateBlock(idx, { distance_km: Number.isFinite(n) ? n : 0 });
                          }}
                          inputMode="decimal"
                        />
                      </div>
                      <div className="pb-2 text-white/60">km</div>
                      <div>
                        <div className="text-xs tracking-widest text-white/60 mb-1">INTENSIDADE</div>
                        <div className="flex gap-2 flex-wrap">
                          {(['leve', 'moderado', 'forte'] as IntensityUI[]).map((it) => (
                            <Toggle
                              key={it}
                              active={String(blk.intensity) === it}
                              onClick={() => updateBlock(idx, { intensity: it })}
                              label={INTENSITY_LABEL[it]}
                            />
                          ))}
                        </div>
                      </div>
                    </div>
                  </div>
                ))}
              </div>

              <div className="flex items-center justify-between mt-5 mb-2">
                <div className="flex items-center gap-2">
                  <span className="text-lg">üßä</span>
                  <div className="font-semibold">Desaquecimento</div>
                </div>
                <Switch checked={includeCooldown} onChange={setIncludeCooldown} />
              </div>

              {includeCooldown ? (
                <div className="grid grid-cols-[1fr_auto_1fr] gap-3 items-end">
                  <div>
                    <div className="text-xs tracking-widest text-white/60 mb-1">DIST√ÇNCIA</div>
                    <Input value={cooldownKmRaw} onChange={setCooldownKmRaw} inputMode="decimal" />
                  </div>
                  <div className="pb-2 text-white/60">km</div>
                  <div>
                    <div className="text-xs tracking-widest text-white/60 mb-1">RITMO</div>
                    <div className="rounded-xl border border-white/10 bg-white/5 px-3 py-2 text-white/80">
                      Livre
                    </div>
                  </div>
                </div>
              ) : null}
            </Card>

            <div className="fixed bottom-0 left-0 right-0 bg-[#07150f]/80 backdrop-blur border-t border-white/10">
              <div className="max-w-2xl mx-auto p-4 flex gap-3">
                <Button onClick={handleSave} disabled={isSaving} variant="primary" className="flex-1">
                  {isSaving ? 'Salvando‚Ä¶' : 'Salvar Altera√ß√µes'}
                </Button>
                <Button onClick={handleShare} disabled={isSaving} variant="secondary" className="flex-1">
                  {isSaving ? 'Gerando link‚Ä¶' : 'Gerar/Copiar link do aluno'}
                </Button>
              </div>
            </div>
          </>
        ) : null}
      </div>
    </div>
  );
}

function Card({ children }: { children: React.ReactNode }) {
  return <div className="rounded-3xl border border-white/10 bg-white/5 p-4 shadow-sm">{children}</div>;
}

function Button({
  children,
  onClick,
  disabled,
  className = '',
  variant = 'primary',
}: {
  children: React.ReactNode;
  onClick?: () => void;
  disabled?: boolean;
  className?: string;
  variant?: 'primary' | 'secondary' | 'ghost';
}) {
  const base = 'rounded-full px-4 py-3 font-semibold transition disabled:opacity-60 disabled:cursor-not-allowed';
  const styles =
    variant === 'primary'
      ? 'bg-[#2CFF88] text-black hover:opacity-90'
      : variant === 'secondary'
      ? 'bg-white/10 text-white hover:bg-white/15'
      : 'bg-transparent text-white/80 hover:text-white';
  return (
    <button type="button" onClick={onClick} disabled={disabled} className={`${base} ${styles} ${className}`}>
      {children}
    </button>
  );
}

function Toggle({ label, active, onClick }: { label: string; active: boolean; onClick: () => void }) {
  return (
    <button
      type="button"
      onClick={onClick}
      className={
        'px-4 py-2 rounded-full border transition text-sm ' +
        (active
          ? 'bg-[#2CFF88] border-[#2CFF88] text-black'
          : 'bg-transparent border-white/15 text-white/85 hover:border-white/25')
      }
    >
      {label}
    </button>
  );
}

function Input({
  value,
  onChange,
  placeholder,
  inputMode,
}: {
  value: string;
  onChange: (v: string) => void;
  placeholder?: string;
  inputMode?: React.HTMLAttributes<HTMLInputElement>['inputMode'];
}) {
  return (
    <input
      value={value}
      onChange={(e) => onChange(e.target.value)}
      placeholder={placeholder}
      inputMode={inputMode}
      className="w-full rounded-xl border border-white/10 bg-white/5 px-4 py-3 text-white placeholder:text-white/35 outline-none focus:border-white/25"
    />
  );
}

function Switch({ checked, onChange }: { checked: boolean; onChange: (v: boolean) => void }) {
  return (
    <button
      type="button"
      onClick={() => onChange(!checked)}
      className={'w-12 h-7 rounded-full border border-white/10 transition relative ' + (checked ? 'bg-[#2CFF88]' : 'bg-white/10')}
      aria-label={checked ? 'Desativar' : 'Ativar'}
    >
      <span
        className={'absolute top-1 left-1 w-5 h-5 rounded-full bg-black/70 transition ' + (checked ? 'translate-x-5' : 'translate-x-0')}
      />
    </button>
  );
}
